import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Footer from '../../components/footer'

import Pic1 from '../../img/cases/05/1.jpg'
import Pic2 from '../../img/cases/05/2.jpg'
import Pic3 from '../../img/cases/05/3.jpg'
import Pic4 from '../../img/cases/05/4.jpg'

function scrollWin() {
  window.scrollTo(0, 0);
}

class Cases05 extends Component {

  componentDidMount = () => this.props.resetScroll()

  render() {
    scrollWin();
    return (
      <div className="wrapper">
        {/* TITLECARD */}
        <div className="titleCardWrapper">
          <h1 className="workNr workColor05">05</h1>
          <div className="titleCard">
            <h1>Zine01</h1>
            <div className="bar"></div>
            <div className="shortInfo">
              <div className="row">
                <p className="title">Opdracht</p>
                <p className="info">Fotografie Zine</p>
              </div>
              <div className="row">
                <p className="title">Opdrachtgever</p>
                <p className="info">n.v.t.</p>
              </div>
              <div className="row">
                <p className="title">Jaar</p>
                <p className="info">2019</p>
              </div>
            </div>
            <p className="paragraph col5">Dit is een persoonlijk project dat ik ben begonnen om te oefenen met fotografie, lay-out design en print. In een tijd waar mensen dagelijks langs tientallen foto's scrollen op social-media, wilde ik iets fysieks en duurzaams ontwerpen. Het is een collectie van foto's met het thema portretten en kleuren. De zine is ontworpen binnen mijn eigen huisstijl.</p>
            <Link to="/cases/04">Terug naar vorige</Link>
          </div>
        </div>

        {/* FIRST IMAGE + BG */}
        <div className="imgBG">
          <div className="imgCon col5">
            <img src={Pic1} alt="mockup" />
          </div>
          <div className="bg col7 workBG05"></div>
        </div>

        {/* OTHER CONTENT */}
        <div className="imgCon col6">
          <img src={Pic3} alt="mockup" />
        </div>

        {/* TEXT AREA
      <div className="textArea">
        <blockquote>Don't think in problems, think in solutions</blockquote>
        <p>Zolang ik me kan herinneren heb ik al interesse in het maken van logo’s, websites en video’s. Naast ontwerpen en ontwikkelen heb ik ook een grote passie voor elektronische muziek en hiphop. Ik ben in mijn vrije tijd dan ook vaak te vinden achter mijn draaitafels. Verder ga ik elke vrijdag met mijn vrienden naar Scouting en speel ik regelmatig een potje pool in de stad. Om mezelf fit te houden doe ik Capoeira en neem ik dagelijks een wandeling.</p>
        <p>Ik hoop dat je nou een klein beeld hebt van wie ik ben, maar ik zou ook graag met jou kennismaken! Neem gerust contact met me op als je interesse hebt ;)</p>
      </div> */}

        {/* CARD */}
        <div className="card col3">
          <div className="title">
            <h1>Digitale versie bekijken?</h1>
            <div className="bar barcenter"></div>
          </div>
          <a className="button" target="_blank" href="https://www.nu.nl">view</a>
        </div>

        <div className="imgCon col6">
          <img src={Pic2} alt="mockup" />
        </div>
        <div className="imgCon col5">
          <img src={Pic4} alt="mockup" />
        </div>

        {/* ENDCARD */}
        <div className="endCard">
          <div className="col4">
            <h1>Genoeg gezien?</h1>
            <div className="bar"></div>
            <Link to="/contact">→ Klik hier om contact op te nemen</Link>
          </div>
          <Footer />
        </div>
      </div>
    );
  }
}

export default Cases05;